import SearchIcon from "@mui/icons-material/Search";
import {
    Box, InputBase, styled
} from "@mui/material";
import { useState } from "react";
import { useSearchParams } from "react-router-dom";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useDebounce from "../../../../utils/useDebounce";


const StyledSearchIcon = styled(SearchIcon)(({ theme }) => ({
    color: theme.palette.text.secondary
}))

const StyledInputBase = styled(InputBase)(({ theme }) => ({
    fontWeight: theme.typography.fontWeightMedium,
    fontSize: theme.typography.button.fontSize
}))

const ProductsSearchForm = () => {
    const [searchParams, setSearchParams] = useSearchParams();

    const [query, setQuery] = useState(searchParams.get('query') || '');

    const debouncedQuery = useDebounce(query, 500);

    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            query: debouncedQuery
        })
    }, [debouncedQuery]);

    return (
        <Box sx={{ px: 2, py: 1.5, display: 'flex' }} className="search-form">
            <Box
                sx={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    flexGrow: 1
                }}
            >
                <StyledSearchIcon sx={{ mr: 1 }} color="inherit" />
                <StyledInputBase
                    value={query}
                    placeholder="جستجو شرح یا کد خدمت..."
                    onChange={e => setQuery(e.target.value)}
                    sx={{ flexGrow: 1 }}
                />
            </Box>
        </Box>
    )
}

export default ProductsSearchForm