import AddIcon from "@mui/icons-material/Add";
import MenuOutlined from "@mui/icons-material/MenuOutlined";
import { Box, Card, Divider, Stack } from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useSnackbar } from "notistack";
import { useSearchParams } from "react-router-dom";

import { useComponent, useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveActionBar from "../../../../components/ResponsiveActionBar";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import useDialogProps from "../../../../utils/useDialogProps";
import useResponsiveTableFetch from "../../../../utils/useResponsiveTableFetch";
import DataTable from "./DataTable";
import FilterForm from "./FilterForm";


const tour = {
    key: 'manage-products',
    steps: [{
        title: "مدیریت خدمتات",
        intro: "در فرآیند مدیریت خدمتات، شما همه خدمتاتیی که با آن‌ها در فرآیندهای مختلف مثل فرآیند ثبت فاکتور فروش، رزرو و یا خرید، استفاده می‌کنید، ثبت و مشاهده می‌کنید.",
    }, {
        title: "خدمت جدید",
        intro: "با کلیک برروی این دکمه و ورود مشخصات خدمت می‌توانید خدمتی جدیدی به لیست خدمتاتی خود اضافه کنید.",
        element: ".add-new",
    }, {
        title: "گروه‌های خدمت",
        intro: "شما می‌توانید به هر خدمت یک گروه اختصاص دهید تا در فرآیندهای دیگر، علاوه بر گزارش براساس خدمت، گزارش براساس گروه را هم مشاهده نمایید.",
        element: ".manage-categories",
    }]
};

const Products = () => {
    const { inject } = useAuth();

    const middleware = useMiddleware('Products.Main');

    const ProductFormDialog = useComponent('Products.ProductFormDialog');
    const ProductDeleteDialog = useComponent('Products.ProductDeleteDialog');

    const [searchParams] = useSearchParams();

    const { data, total, loading, loadMore, reload } = useResponsiveTableFetch({
        handleFetch: params => middleware.list({
            type: 'service',
            ...params,
            ...Object.fromEntries([...searchParams])
        }),
        loadDeps: [searchParams],
        infiniteScrollDefaultParams: {
            page: searchParams.has('page') ? searchParams.get('page') : 0,
            perPage: searchParams.has('perPage') ? searchParams.get('perPage') : 25,
        }
    })

    const { enqueueSnackbar } = useSnackbar();

    const [formDialogProps, openFormDialog, closeFormDialog] = useDialogProps("form");
    const handleCreateOrUpdate = (message) => {
        enqueueSnackbar(message);
        reload();
        closeFormDialog();
    }

    const [deleteDialogProps, openDeleteDialog, closeDeleteDialog] = useDialogProps("delete", {
        hashDeps: ["product"]
    });
    const handleDelete = () => {
        enqueueSnackbar('خدمت حذف گردید');
        closeDeleteDialog();
        reload();
    }

    const injectedHooks = Object.assign({}, ...(inject('Products', 'ProductsList.Hooks') || []));

    const task = useTask('Services.manage');

    return (
        <TabView {...task} tour={tour}>
            <ResponsiveContainer>
                <Box sx={{ display: "flex", mb: { xs: 0, sm: 1 } }}>
                    <Box sx={{ flexGrow: 1 }} />
                    <Stack direction="row" spacing={1}>
                        <ResponsiveActionBar
                            actions={sortBy([
                                {
                                    title: 'خدمت جدید',
                                    icon: <AddIcon />,
                                    onClick: () => {
                                        openFormDialog({
                                            onCreateOrUpdate: () => handleCreateOrUpdate(`مشخصات خدمت ثبت گردید.`)
                                        })
                                    },
                                    authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('product:edit'),
                                    priority: 100
                                },
                                ...flatten((inject('Products', 'ProductsList.ActionsList', { data, loading, reload, ...injectedHooks }) || []))
                            ], o => o.priority)}
                            ResponsiveActionIcon={<MenuOutlined />}
                            responsiveActionProps={{ color: 'primary' }}
                        />
                    </Stack>
                </Box>

                <Card variant="outlined">
                    <FilterForm />
                    <Divider />
                    <DataTable
                        products={data}
                        loading={loading}
                        meta={{ total }}
                        infiniteScrollProps={{
                            loading: false,
                            hasNextPage: data.length < total,
                            onLoadMore: loadMore
                        }}
                        onClickEdit={item => {
                            openFormDialog({
                                defaultValues: item,
                                onCreateOrUpdate: () => handleCreateOrUpdate(`مشخصات خدمت بروزرسانی گردید.`)
                            })
                        }}
                        onClickDelete={(product) => {
                            openDeleteDialog({ product });
                        }}
                    />
                </Card>

                <ProductFormDialog
                    defaultValues={{ type: 'service' }}
                    {...formDialogProps}
                />

                <ProductDeleteDialog
                    {...deleteDialogProps}
                    onDelete={handleDelete}
                />

                {inject('Products', 'ProductsList.Footer', { data, loading, reload, ...injectedHooks })}
            </ResponsiveContainer>
        </TabView>
    )
}


export default Products;