import DeleteOutlined from "@mui/icons-material/DeleteOutlined";
import EditOutlined from "@mui/icons-material/EditOutlined";
import { Box, ListItemButton, ListItemText } from "@mui/material";
import flatten from "lodash/flatten";
import sortBy from "lodash/sortBy";
import { useState } from "react";
import { useSearchParams } from "react-router-dom";
import { useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import ResponsiveTable from "../../../../components/ResponsiveTable";
import Text from "../../../../components/Text";
import useAfterFirstRender from "../../../../utils/useAfterFirstRender";
import useGetRowNumber from "../../../../utils/useGetRowNumber";
import useNavigateTask from "../../../../utils/useNavigateTask";


const ProductsTable = ({
    loading,
    products,
    meta,
    infiniteScrollProps,
    onClickEdit,
    onClickDelete,
}) => {
    const { inject } = useAuth();

    const [searchParams, setSearchParams] = useSearchParams();

    const [sort, setSort] = useState(searchParams.has('sort') ? searchParams.get('sort') : '-createdAt');

    useAfterFirstRender(() => {
        setSearchParams({
            ...Object.fromEntries(searchParams),
            sort
        })
    }, [sort]);

    const getRowNumber = useGetRowNumber();

    const navigateTask = useNavigateTask();
    const taskDetails = useTask('Products.details');


    return (
        <>
            <ResponsiveTable
                data={products}
                loading={loading}
                infiniteScrollProps={infiniteScrollProps}
                definitions={sortBy([
                    {
                        title: 'ردیف',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        renderCell: (row, index) => <Text>{getRowNumber(index)}</Text>,
                        priority: 100
                    }, {
                        field: 'code',
                        title: 'کد خدمت',
                        headerCellProps: {
                            align: "center"
                        },
                        rowCellProps: {
                            align: "center",
                            width: 100
                        },
                        render: row => <Text>{row.code}</Text> || '-',
                        priority: 200
                    }, {
                        title: 'خدمت',
                        rowCellProps: {
                            width: 400
                        },
                        renderCell: row => (
                            <Box sx={{ display: 'flex', alignItems: 'center' }}>
                                {/* <Avatar
                                    sx={{ width: 30, height: 30, fontSize: '1.15em', mr: 1 }}
                                    src={row.avatar}
                                /> */}
                                <strong><Text>{row.name}</Text></strong>
                            </Box>
                        ),
                        priority: 300
                    },
                    ...flatten((inject('Products', 'ProductsList.definitions') || []))
                ], o => o.priority)}
                renderSheetTitle={row => <Text>{row.name}</Text>}
                renderListItemChildren={(row) => (
                    <ListItemButton
                        onClick={() => navigateTask(taskDetails(row))}
                    >
                        {/* <ListItemAvatar>
                            <Avatar src={row.avatar} />
                        </ListItemAvatar> */}
                        <ListItemText
                            primary={<strong>{row.name}</strong>}
                        // secondary={row.roles && row.roles.length > 0 ? <Text>{row.roles.map(r => r.name).join(', ')}</Text> : '-'}
                        />
                    </ListItemButton>
                )}

                tableRowProps={row => ({
                    hover: true,
                    striped: true,
                    onClick: () => navigateTask(taskDetails(row))
                })}

                paginationProps={{
                    count: meta.total,
                    rowsPerPage: searchParams.has('perPage') ? Number(searchParams.get('perPage')) : 25,
                    page: searchParams.has('page') ? Number(searchParams.get('page')) : 0,
                    onPageChange: (e, page) => setSearchParams({ ...Object.fromEntries([...searchParams]), page }),
                    onRowsPerPageChange: e => setSearchParams({ ...Object.fromEntries([...searchParams]), perPage: e.target.value })
                }}

                actions={[
                    {
                        title: 'ویرایش',
                        icon: <EditOutlined />,
                        onClick: onClickEdit,
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:edit')
                    },
                    {
                        title: 'حذف',
                        icon: <DeleteOutlined />,
                        onClick: person => onClickDelete(person, false),
                        authorize: ({ hasRole, hasPermission }) => hasRole('owner') || hasPermission('person:delete')
                    }
                ]}
            />
        </>
    )
}

export default ProductsTable